import { Fragment } from "react";
import { RiNextjsLine } from "react-icons/ri";


const expertData = [
    {
        icon : <RiNextjsLine />,
        text : 'Next.Js'
    },
]

export default function Experts(){
    return <Fragment>
{
    expertData.map((item, index)=>{
        return<div key={index}>
            <h4>{item.text}</h4>
        </div>
    })
}

    </Fragment>
}