import { Fragment } from "react";

const masonaryData = [
  {
    bg: "/images/Masonary/m-1.webp",
    logo: "",
    text: "Mesonary",
  },
  {
    bg: "/images/Masonary/m-2.webp",
    logo: "",
    text: "Mesonary",
  },
  {
    bg: "/images/Masonary/m-3.webp",
    logo: "",
    text: "Mesonary",
  },
  {
    bg: "/images/Masonary/m-4.webp",
    logo: "",
    text: "Mesonary",
  },
  {
    bg: "/images/Masonary/m-5.webp",
    logo: "",
    text: "Mesonary",
  },
];

export default function MasonaryCard() {
  return (
    <Fragment>
      {masonaryData.map((item, index) => {
        return (
          <div
            key={index}
            style={{ backgroundImage: `url(${item.bg})` }}
            className="bg-contain bg-center bg-no-repeat h-full w-full "
          >
            <h1>{item.text}</h1>
          </div>
        );
      })}
    </Fragment>
  );
}
